@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro.woff2") format("woff2"), url("../fonts/GothamPro.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro-Bold.woff2") format("woff2"), url("../fonts/GothamPro-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: "MuseoSansCyrl";
  src: url("../fonts/MuseoSansCyrl-500.woff2") format("woff2"), url("../fonts/MuseoSansCyrl-500.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

body {
  font-family: "Gotham Pro", "Verda na", sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.0025em; }

.header__logo img {
  display: block;
  width: 112px;
  height: auto; }

.container {
  margin: 0 auto;
  padding-left: 150px;
  padding-right: 150px; }

.header {
  min-width: 900px;
  display: flex;
  padding-top: 16px;
  margin-bottom: 44px; }

.header__nav {
  flex-grow: 2; }

.header__icon {
  fill: #8391b2;
  position: absolute;
  top: 1px;
  left: 0px; }

.menu__item {
  margin-left: 55px; }

.menu__link {
  position: relative;
  color: #8391b2;
  text-decoration: none;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 0.0025em;
  padding-left: 20px; }
  .menu__link:hover {
    color: rgba(131, 145, 178, 0.6); }
  .menu__link:hover .header__icon {
    color: rgba(131, 145, 178, 0.6);
    fill: rgba(131, 145, 178, 0.6); }
  .menu__link:active,
  .menu__link:active .header__icon {
    fill: #29435d;
    color: #29435d; }

.header__menu {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  padding-right: 8px; }

@media (max-width: 1199px) {
  .header {
    min-width: 708px; }
  .container {
    padding: 16px 30px; }
  .header__menu {
    padding: 0; }
  .header__icon {
    top: -15px;
    left: 8px; }
  .header__logo img {
    display: block;
    width: 82px;
    height: auto; }
  .menu__link {
    font-size: 0;
    line-height: 0; }
  .menu__item {
    margin-left: 33px; } }

@media (max-width: 767px) {
  .header {
    min-width: 280px;
    margin-bottom: 19px; }
  .container {
    padding: 10px 20px; }
  .header__logo img {
    width: 52px; }
  .header__icon {
    width: 10px;
    height: 10px; }
  .menu__item {
    margin-left: 2px; } }

.about {
  padding-top: 50px;
  position: relative; }
  .about::after {
    content: "";
    position: absolute;
    background-image: url("/img/about.png");
    background-repeat: no-repeat;
    top: -3px;
    right: -2px;
    width: 41%;
    height: 363px; }

.about__wrapper {
  max-width: 528px; }

.about__header {
  font-family: "Gotham Pro", "Verdana", sans-serif;
  font-weight: bold;
  font-size: 32px;
  line-height: 38px;
  letter-spacing: 0.0025em;
  color: #29435d;
  margin: 0;
  padding: 0;
  margin-bottom: 19px; }

.about__info {
  font-family: "Gotham Pro";
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.0025em;
  color: #8391b2;
  margin: 0;
  padding: 0;
  margin-bottom: 30px; }

.about__btn {
  width: 160px;
  height: 32px;
  box-sizing: border-box;
  background: #2F93FE;
  border-radius: 6px;
  padding: 8px 20px;
  font-family: "MuseoSansCyrl", "Verdana", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.0275em;
  text-decoration: none;
  color: #ffffff; }
  .about__btn:hover {
    background: #71b6ff; }
  .about__btn:active {
    background: #0971f5; }
  .about__btn:disabled {
    background: #c8c8c8; }

@media (min-width: 768px) and (max-width: 1190px) {
  .about {
    padding-top: 17px; }
    .about::after {
      content: "";
      position: absolute;
      background-image: url("/img/about-tablet.png");
      top: -11px;
      right: 12px;
      height: 247px; }
  .about__wrapper {
    width: 57%; }
  .about__header {
    font-weight: bold;
    font-size: 26px;
    line-height: 31px; }
  .about__info {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px; } }

@media (max-width: 767px) {
  .about {
    padding-top: 2px;
    padding-bottom: 175px; }
    .about::after {
      background-image: url("/img/about-mobile.png");
      width: 59%;
      height: 175px;
      min-width: 209px;
      top: 158px;
      right: 28px; }
  .about__header {
    font-weight: bold;
    font-size: 18px;
    line-height: 22px; }
  .about__info {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    margin-bottom: 25px; }
  .about__btn {
    width: 160px;
    height: 32px;
    box-sizing: border-box;
    background: #2F93FE;
    border-radius: 6px;
    padding: 7px 12px;
    font-size: 10px;
    line-height: 24px; } }
